























































































import { Component, Prop, Watch, Emit, Vue } from 'vue-property-decorator';
import moment from 'moment';

@Component
export default class CTimeDiff extends Vue {
  // Data
  private NOW: any = moment(new Date());
  private dataStartPoint: {
    year: number,
    month: number,
    day: number,
    hour: number,
    minute: number,
    second: number,
  } = {
    year: 0,
    month: 0,
    day: 0,
    hour: 0,
    minute: 0,
    second: 0,
  };
  private dataEndPoint: {
    year: number,
    month: number,
    day: number,
    hour: number,
    minute: number,
    second: number,
  } = {
    year: 0,
    month: 0,
    day: 0,
    hour: 0,
    minute: 0,
    second: 0,
  };
  private dataPointDiffTotal: string = '';
  private dataPointDiffYear: number = 0;
  private dataPointDiffMonth: number = 0;
  private dataPointDiffDay: number = 0;
  private dataPointDiffHour: number = 0;
  private dataPointDiffMinute: number = 0;
  private dataPointDiffSecond: number = 0;
  private dataYears: number[] = [];
  private dataMonths: number[] = [];
  private dataStartPointDays: number[] = [];
  private dataEndPointDays: number[] = [];
  private dataHours: number[] = [];
  private dataMinutesAndSeconds: number[] = [];
  private dataAlert: string = '';

  // Watch
  @Watch('dataStartPoint')
  onStartPointChange(newValue: string, oldValue: string): void {
  }
  @Watch('dataEndPoint')
  onEndPointChange(newValue: string, oldValue: string): void {
  }
  @Watch('dataPointDiff')
  onPointDiffChange(newValue: string, oldValue: string): void {
  }

  // Lifecycle hook
  created(): void {
    let startPoint: any = this.NOW.clone();
    // let endPoint: any = this.NOW.clone().subtract(1, 'weeks');
    let endPoint: any = this.NOW.clone();
    this.formatEndPoint(startPoint);
    this.formatStartPoint(endPoint);
    this.initYears();
    this.initMonths();
    this.updateDays(startPoint, this.dataStartPointDays);
    this.updateDays(endPoint, this.dataEndPointDays);
    this.initHours();
    this.initMinutesAndSeconds();
    this.diffDatetime();
  }

  // Methods
  private formatStartPoint(startPointDate: any): void {
    this.dataStartPoint = {
      year: startPointDate.year(),
      month: startPointDate.month(),
      day: startPointDate.date(),
      hour: startPointDate.hour(),
      minute: startPointDate.minutes(),
      second: startPointDate.second(),
    };
  }
  private formatEndPoint(endPointDate: any): void {
    this.dataEndPoint = {
      year: endPointDate.year(),
      month: endPointDate.month(),
      day: endPointDate.date(),
      hour: endPointDate.hour(),
      minute: endPointDate.minutes(),
      second: endPointDate.second(),
    };
  }
  private initYears(): void {
    const YEAR_TERM: number = 120;
    let startPointYear: number;
    let endPointYear: number;
    startPointYear = this.NOW.clone().subtract(YEAR_TERM, 'years').year();
    endPointYear = this.NOW.clone().add(YEAR_TERM, 'years').year();
    let i: number = 0;
    while (startPointYear + i <= endPointYear) {
      this.dataYears.push(startPointYear + i);
      i++;
    }
  }
  private initMonths(): void {
    const MONTH_TERM: number = 12;
    for (let i: number = 0; i < MONTH_TERM; i++) {
      this.dataMonths.push(i);
    }
  }
  private calcNaturalMonth(month: number): number {
    return month + 1;
  }
  private updateDays(datetime: any, days: number[]): void {
    let endPointDay: number = datetime.clone().endOf('month').date();
    if (days.length < endPointDay) {
      for (let i: number = days.length + 1; i <= endPointDay; i++) {
        days.push(i);
      }
    } else if (endPointDay < days.length) {
      for (let i: number = days.length; endPointDay < i; i--) {
        days.pop();
      }
    }

  }
  private initHours(): void {
    const HOUR_TERM: number = 24;
    for (let i: number = 0; i < HOUR_TERM; i++) {
      this.dataHours.push(i);
    }
  }
  private initMinutesAndSeconds(): void {
    const MINUTE_AND_SECOND_TERM: number = 60;
    for (let i: number = 0; i < MINUTE_AND_SECOND_TERM; i++) {
      this.dataMinutesAndSeconds.push(i);
    }
  }
  private toMoment(point: {year: number, month: number, day: number, hour: number, minute: number, second: number}): any {
    let startOfMonth: any = moment(new Date(point.year, point.month, 1));
    let endDayOfMonth: number = startOfMonth.endOf('month').date();
    if (endDayOfMonth < point.day) {
      point.day = endDayOfMonth;
    }
    return moment(new Date(
      point.year,
      point.month,
      point.day,
      point.hour,
      point.minute,
      point.second,
    ));
  }
  private diffDatetime(): void {
    let startMoment: any = this.toMoment(this.dataStartPoint);
    let endMoment: any = this.toMoment(this.dataEndPoint);
    if (endMoment.diff(startMoment) < 0) {
      this.dataAlert = '開始日時を終了日時よりも前にしてください。';
    } else {
      // Alert
      this.dataAlert = '';
      // Each diff
      this.dataPointDiffYear = endMoment.diff(startMoment, 'years');
      this.dataPointDiffMonth = endMoment.diff(startMoment, 'months');
      this.dataPointDiffDay = endMoment.diff(startMoment, 'days');
      this.dataPointDiffHour = endMoment.diff(startMoment, 'hours');
      this.dataPointDiffMinute = endMoment.diff(startMoment, 'minutes');
      this.dataPointDiffSecond = endMoment.diff(startMoment, 'seconds');
      // Total diff
      let pointDiffTotal: string = '';
      let yearDiff: number = endMoment.diff(startMoment, 'years');
      pointDiffTotal += String(yearDiff) + '年 ';
      startMoment.add(yearDiff, 'years');
      let monthDiff: number = endMoment.diff(startMoment, 'months');
      pointDiffTotal += String(monthDiff) + 'ヵ月 ';
      startMoment.add(monthDiff, 'months');
      let dayDiff: number = endMoment.diff(startMoment, 'days');
      pointDiffTotal += String(dayDiff) + '日 ';
      startMoment.add(dayDiff, 'days');
      let hourDiff: number = endMoment.diff(startMoment, 'hours');
      pointDiffTotal += String(hourDiff) + '時間 ';
      startMoment.add(hourDiff, 'hours');
      let minuteDiff: number = endMoment.diff(startMoment, 'minutes');
      pointDiffTotal += String(minuteDiff) + '分 ';
      startMoment.add(minuteDiff, 'minutes');
      let secondDiff: number = endMoment.diff(startMoment, 'seconds');
      pointDiffTotal += String(secondDiff) + '秒 ';
      this.dataPointDiffTotal = pointDiffTotal;
    }
  }
  private listenStartPointYearChange($event: Event): void {
    this.dataStartPoint.year = Number((<HTMLInputElement>(<any>$event).target).value);
    let datetime: any = this.toMoment(this.dataStartPoint);
    this.updateDays(datetime, this.dataStartPointDays);
    this.diffDatetime();
  }
  private listenStartPointMonthChange($event: Event): void {
    this.dataStartPoint.month = Number((<HTMLInputElement>(<any>$event).target).value);
    let datetime: any = this.toMoment(this.dataStartPoint);
    this.updateDays(datetime, this.dataStartPointDays);
    this.diffDatetime();
  }
  private listenStartPointDayChange($event: Event): void {
    this.dataStartPoint.day = Number((<HTMLInputElement>(<any>$event).target).value);
    this.diffDatetime();
  }
  private listenStartPointHourChange($event: Event): void {
    this.dataStartPoint.hour = Number((<HTMLInputElement>(<any>$event).target).value);
    this.diffDatetime();
  }
  private listenStartPointMinuteChange($event: Event): void {
    this.dataStartPoint.minute = Number((<HTMLInputElement>(<any>$event).target).value);
    this.diffDatetime();
  }
  private listenStartPointSecondChange($event: Event): void {
    this.dataStartPoint.second = Number((<HTMLInputElement>(<any>$event).target).value);
    this.diffDatetime();
  }
  private listenEndPointYearChange($event: Event): void {
    this.dataEndPoint.year = Number((<HTMLInputElement>(<any>$event).target).value);
    let datetime: any = this.toMoment(this.dataEndPoint);
    this.updateDays(datetime, this.dataEndPointDays);
    this.diffDatetime();
  }
  private listenEndPointMonthChange($event: Event): void {
    this.dataEndPoint.month = Number((<HTMLInputElement>(<any>$event).target).value);
    let datetime: any = this.toMoment(this.dataEndPoint);
    this.updateDays(datetime, this.dataEndPointDays);
    this.diffDatetime();
  }
  private listenEndPointDayChange($event: Event): void {
    this.dataEndPoint.day = Number((<HTMLInputElement>(<any>$event).target).value);
    this.diffDatetime();
  }
  private listenEndPointHourChange($event: Event): void {
    this.dataEndPoint.hour = Number((<HTMLInputElement>(<any>$event).target).value);
    this.diffDatetime();
  }
  private listenEndPointMinuteChange($event: Event): void {
    this.dataEndPoint.minute = Number((<HTMLInputElement>(<any>$event).target).value);
    this.diffDatetime();
  }
  private listenEndPointSecondChange($event: Event): void {
    this.dataEndPoint.second = Number((<HTMLInputElement>(<any>$event).target).value);
    this.diffDatetime();
  }
}
